'use client';

import { useState, useEffect, FormEvent } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import Link from 'next/link';
import { ArrowLeft, Plus, Trash2, Save, Loader2 } from 'lucide-react';
import { useParams } from 'next/navigation'; 

// UI Components
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from "@/components/ui/select";
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from '@/components/ui/table';

// --- TIPE DATA ---
interface Satuan { nama_satuan: string; }
interface BahanBaku {
    id: number;
    nama_bahan_baku: string;
    satuan: Satuan;
}
interface ResepItem {
    id: number;
    bahan_baku_id: number;
    jumlah: number;
    keterangan: string;
    bahan_baku: BahanBaku;
}
interface ProdukDetail {
    id: number;
    nama_produk: string;
    harga_jual: number;
    satuan: Satuan;
}

export default function ManajemenResepPage() {
    const params = useParams();
    const produkId = params.id; 
    const [produk, setProduk] = useState<ProdukDetail | null>(null);
    const [resepList, setResepList] = useState<ResepItem[]>([]);
    const [bahanBakuList, setBahanBakuList] = useState<BahanBaku[]>([]);
    const [loading, setLoading] = useState(true);

    const [selectedBahan, setSelectedBahan] = useState('');
    const [jumlah, setJumlah] = useState('');
    const [isAdding, setIsAdding] = useState(false);

    const fetchData = async () => {
        setLoading(true);
        try {
            const [resProduk, resResep, resBahan] = await Promise.all([
                axios.get(`/api/produkRestos/${produkId}`),
                axios.get(`/api/produkRestos/${produkId}/resep`),
                axios.get(`/api/bahanBakus`)
            ]);

            setProduk(resProduk.data);
            setResepList(resResep.data);
            setBahanBakuList(resBahan.data);
        } catch (error) {
            console.error(error);
            toast.error("Gagal memuat data resep.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        if (produkId) fetchData();
    }, [produkId]);

    const handleAddBahan = async (e: FormEvent) => {
        e.preventDefault();
        if (!selectedBahan || !jumlah) {
            toast.error("Pilih bahan dan isi jumlah!");
            return;
        }

        setIsAdding(true);
        try {
            await axios.post(`/api/produkRestos/${produkId}/resep`, {
                bahan_baku_id: selectedBahan,
                jumlah: jumlah,
                keterangan: '-'
            });

            toast.success("Bahan berhasil ditambahkan!");
            setJumlah(''); 
            const resNewResep = await axios.get(`/api/produkRestos/${produkId}/resep`);
            setResepList(resNewResep.data);

        } catch (error: any) {
            toast.error(error.response?.data?.message || "Gagal menambah bahan.");
        } finally {
            setIsAdding(false);
        }
    };

    const handleDeleteItem = async (resepId: number) => {
        if (!confirm("Hapus bahan ini dari resep?")) return;

        try {
            await axios.delete(`/api/reseps/${resepId}`);
            toast.success("Bahan dihapus dari resep.");
            setResepList(prev => prev.filter(item => item.id !== resepId));
        } catch (error) {
            toast.error("Gagal menghapus item.");
        }
    };

    if (loading) return <div className="p-8 text-center">Memuat Resep...</div>;
    if (!produk) return <div className="p-8 text-center text-red-500">Produk tidak ditemukan.</div>;

    return (
        <div className="space-y-6 max-w-5xl mx-auto">
            
            <div className="flex items-center gap-4">
                <Link href="/admin/produk-resto">
                    <Button variant="outline" size="icon">
                        <ArrowLeft className="h-4 w-4" />
                    </Button>
                </Link>
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Resep: {produk.nama_produk}</h1>
                    <p className="text-gray-500 text-sm">
                        Tentukan bahan baku untuk 1 {produk.satuan?.nama_satuan} {produk.nama_produk}.
                    </p>
                </div>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
                
                <Card className="lg:col-span-1 h-fit shadow-md border-t-4 border-t-[#D93F21]">
                    <CardHeader>
                        <CardTitle className="text-lg">Tambah Komposisi</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <form onSubmit={handleAddBahan} className="space-y-4">
                            <div className="space-y-2">
                                <Label>Pilih Bahan Baku</Label>
                                <Select value={selectedBahan} onValueChange={setSelectedBahan}>
                                    <SelectTrigger>
                                        <SelectValue placeholder="Cari bahan..." />
                                    </SelectTrigger>
                                    <SelectContent>
                                        {bahanBakuList.map((bahan) => (
                                            <SelectItem key={bahan.id} value={bahan.id.toString()}>
                                                {bahan.nama_bahan_baku} ({bahan.satuan?.nama_satuan})
                                            </SelectItem>
                                        ))}
                                    </SelectContent>
                                </Select>
                            </div>

                            <div className="space-y-2">
                                <Label>Jumlah Dibutuhkan</Label>
                                <div className="flex items-center gap-2">
                                    <Input 
                                        type="number" 
                                        placeholder="0" 
                                        value={jumlah}
                                        onChange={e => setJumlah(e.target.value)}
                                        required
                                        step="0.01"
                                        className="flex-1"
                                    />
                                    <span className="text-sm font-medium text-gray-500 bg-gray-100 px-3 py-2 rounded-md">
                                        {bahanBakuList.find(b => b.id.toString() === selectedBahan)?.satuan?.nama_satuan || '-'}
                                    </span>
                                </div>
                                <p className="text-xs text-gray-400">
                                    Contoh: Jika Nasi Goreng butuh 100gr beras, isi 100.
                                </p>
                            </div>

                            <Button type="submit" disabled={isAdding} className="w-full bg-[#D93F21] hover:bg-[#b9351b]">
                                {isAdding ? <Loader2 className="h-4 w-4 animate-spin" /> : <><Plus className="h-4 w-4 mr-2" /> Tambahkan ke Resep</>}
                            </Button>
                        </form>
                    </CardContent>
                </Card>

                <Card className="lg:col-span-2 shadow-sm">
                    <CardHeader>
                        <CardTitle className="text-lg">Komposisi Saat Ini</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="rounded-md border">
                            <Table>
                                <TableHeader>
                                    <TableRow className="bg-gray-50">
                                        <TableHead>Nama Bahan</TableHead>
                                        <TableHead>Jumlah</TableHead>
                                        <TableHead>Satuan</TableHead>
                                        <TableHead className="text-right">Aksi</TableHead>
                                    </TableRow>
                                </TableHeader>
                                <TableBody>
                                    {resepList.length === 0 ? (
                                        <TableRow>
                                            <TableCell colSpan={4} className="h-32 text-center text-gray-500">
                                                Belum ada komposisi resep.
                                            </TableCell>
                                        </TableRow>
                                    ) : (
                                        resepList.map((item) => (
                                            <TableRow key={item.id}>
                                                <TableCell className="font-medium">
                                                    {item.bahan_baku?.nama_bahan_baku}
                                                </TableCell>
                                                <TableCell>
                                                    {Number(item.jumlah)}
                                                </TableCell>
                                                <TableCell>
                                                    <span className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-blue-50 text-blue-700">
                                                        {item.bahan_baku?.satuan?.nama_satuan}
                                                    </span>
                                                </TableCell>
                                                <TableCell className="text-right">
                                                    <Button 
                                                        variant="ghost" 
                                                        size="icon" 
                                                        className="h-8 w-8 text-red-600 hover:bg-red-50"
                                                        onClick={() => handleDeleteItem(item.id)}
                                                    >
                                                        <Trash2 className="h-4 w-4" />
                                                    </Button>
                                                </TableCell>
                                            </TableRow>
                                        ))
                                    )}
                                </TableBody>
                            </Table>
                        </div>
                    </CardContent>
                </Card>
            </div>
        </div>
    );
}